.custom-table {
    .default-custom-table {
        /deep/ .bk-table-pagination-wrapper {
            background-color: #fff;
        }

        /deep/ .is-first {
            .cell {
                -webkit-line-clamp: 2;
            }
        }

        // .status-color {
        //     display: inline-block;
        //     width: 10px;
        //     height: 10px;
        //     border-radius: 50%;
        //     margin-right: 10px;
        // }

        .dot-menu-trigger {
            position: relative;
            bottom: 2px;
            display: block;
            width: 30px;
            height: 30px;
            line-height: 30px;
            border-radius: 50%;
            text-align: center;
            font-size: 0;
            cursor: pointer;
        }

        .dot-menu-trigger:hover {
            color: #3A84FF;
            background-color: #DCDEE5;
        }

        .dot-menu-trigger:before {
            content: "";
            color: #3A84FF;
            display: inline-block;
            width: 3px;
            height: 3px;
            border-radius: 50%;
            background-color: currentColor;
            box-shadow: 0 -4px 0 currentColor, 0 4px 0 currentColor;
        }
    }
}
