$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景





























































































.stats-card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.show-vertical {
    flex-direction: column;
    justify-content: space-between;

    .card-body {
        margin-left: 0;
    }
}

.card-body {
    flex-grow: 1;
    display: flex;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    margin-left: 16px;
    background: #ffffff;
}

.card-body:hover {
    cursor: pointer;
    box-shadow: 0 1px 5px 0 rgba(25, 40, 60, 0.2);
    /*卡片动效*/
    /*.card-icon {*/
    /*    animation: enlarge 100ms linear;*/
    /*    animation-fill-mode: forwards;*/
    /*}*/
}

.vertical-body {
    flex-direction: column-reverse;
    justify-content: space-around;
    align-items: center;
    min-height: 230px;
    padding: 20px 20px;
}

.horizontal-body {
    flex-direction: row;
    justify-content: space-between;
    max-height: 105px;
    padding: 20px 24px;
}

.card-body:nth-child(1) {
    margin-left: 0;
}

.card-text {
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-text-num {
    max-width: 130px;
    font-size: 24px;
    font-weight: bold;
    color: #63656E;
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-text-name {
    font-size: 12px;
    font-weight: 400;
    color: #979BA5;
    display: flex;
    flex-direction: row;
}

.card-name {
    max-width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.card-icon {
    width: 60px;
    height: 60px;
    text-align: center;
    font-size: 30px;

    .iconfont {
        font-size: inherit;
    }
}

.card-icon-background {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.card-icon-circle {
    border-radius: 50%;
}

.card-icon-hexagon {
    background: url("../../assets/svg/background-hexagon.svg") 3px -1px no-repeat !important;
}

/*放大特效*/
@keyframes enlarge {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1.2);
    }
}

