// 基础颜色统一配置
$head-theme: 'light'; //选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; //导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; //主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; //图表颜色
$chart-second: #b0d7ff; //图表颜色
$bask-tips-border: #d7ebff; //提示框边框
$base-card: #e9f5ff; //卡片，信息提示窗背景
$base-color-blue: #399CFF; //蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; //次要按钮
$primary-hover: #63D6FA; //hover

$info-color: #40C6EF; //次要按钮
$info-hover: #66D1F2; //hover
$info-border: #d8f4ff; //边框颜色
$info-back: #ebf9ff; //背景颜色


$success-color: #3de586; //操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; //hover
$success-border: #DEF6E8; //边框颜色
$success-back: #EEFAF3; //背景颜色

$warn-color: #FEA30D; //警告，谨慎使用
$warn-hover: #F8CC64; //hover
$warn-border: #FFEDCF; //边框颜色
$warn-back: #FEF5E6; //背景颜色

$error-color: #ED3F3F; //操作警告 信息提醒 失败信息
$error-hover: #F39082; //hover
$error-border: #FBD9D9; //边框颜色
$error-back: #FDEBEB; //背景颜色

$cancel-color: #C9C9C9; //取消选择 边框颜⾊
$cancel-hover: #A9A9A9; //hover

$nav-color: #90BFE1; //导航hover

$sup-color: #F4F9FA; //辅色

$font-color: #282A2B; //主要⽂字 标题

$font-second: #5F6568; //常规文字 正文

$font-third: #888E94; //次要文字

$border-color: #DADEE2; //边框

$Dividing-line-color: #E7EBEE; //分割线

$background-color: #EFF2F5; //背景

$white-color: #fff; //白色

$auxiliary-color: #F7F8F9; //辅助背景







































































.stats-card {
    width: 100%;
    display: flex;
    flex-direction: row;
}

.card-body {
    flex-grow: 1;
    display: flex;
    height: 100%;
    background: #ffffff;
    flex-direction: row;
    align-items: center;
    padding: 3px 24px;
    border-left: 1px solid #DCDEE5;
}

.card-body:nth-child(1) {
    border-left: none;
}

.card-text {
    height: 100%;
    margin-left: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.card-text-num {
    max-width: 130px;
    font-size: 24px;
    font-weight: bold;
    color: #63656E;
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-text-name {
    font-size: 12px;
    font-weight: 400;
    color: #979BA5;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.card-name {
    max-width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
    text-align: center;
}

.card-icon {
    width: 60px;
    height: 60px;
    text-align: center;
    font-size: 30px;

    .iconfont {
        font-size: inherit;
    }
}

.card-icon-background {
    color: #3A84FF;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: url("../../assets/svg/background-hexagon.svg") 3px 0 no-repeat;
}

.no-icon {
    margin: 0 auto !important;
}
